import React from 'react'
import Button from '../UI/Button';
import Me from '../../images/ceo-img.png';

export default function Index() {
    return (
        <>
            <div className="header">
                <div className="heading">
                    <p>Hello, I am <span>Yogesh Giri</span></p>
                    <h1>Web Developer</h1>
                    <p><small>A passionate full stack web developer from India.</small></p>
                    <Button label="About Me" anchor="#" />
                    <Button label="My Resume" inverse={true} anchor="https://github.com/yogeshgiri904"/>
                </div>
                <div className="heading-img">
                    <img src={Me} alt="ME"/>
                </div>
            </div>
        </>   
    );
}